<template>
    <div>
        <iviewTable border :columns="classScheduleCom" :data="classDataCom" :disabled-hover="true" :show-header="true" v-if="this.$store.state.SyllabusList">
        </iviewTable>
        <Spin size="large" style="height: 100%" fix v-if="!this.$store.state.SyllabusList || spanShow"></Spin>
    </div>
</template>

<script>
export default {
    name: 'classSchedule',
    components: {
        iviewTable: () => import('iview/src/components/table'),
        Spin: () => import('iview/src/components/spin')
    },
    data() {
        return {
            classScheduleTime: [
                {
                    title: '星期',
                    key: 'Course-Week'
                },
                {
                    title: '一',
                    key: 'Monday'
                },
                {
                    title: '二',
                    key: 'Tuesday'
                },
                {
                    title: '三',
                    key: 'Wednesday'
                },
                {
                    title: '四',
                    key: 'Thursday'
                },
                {
                    title: '五',
                    key: 'Friday'
                },
                {
                    title: '六',
                    key: 'Saturday'
                },
                {
                    title: '日',
                    key: 'Weekday'
                }
            ],
            classDataArray: [
                {
                    'Course-Week': '第一节'
                },
                {
                    'Course-Week': '第二节'
                },
                {
                    'Course-Week': '第三节'
                },
                {
                    'Course-Week': '第四节'
                },
                {
                    'Course-Week': '第五节'
                }
            ],
            classData: [
                {
                    'Course-Week': '第一节',
                    Monday: '',
                    Tuesday: '',
                    Wednesday: '',
                    Thursday: '',
                    Friday: '',
                    Saturday: '',
                    Weekday: ''
                }
            ]
        }
    },
    props: ['spanShow'],
    computed: {
        classScheduleCom() {
            const Vm = this
            let tempArray = []
            tempArray = Vm.classScheduleTime.map((item) => {
                let isMobile = this.$cjc.browser.versions.mobile
                let obj = {
                    title: item.title,
                    key: item.key,
                    // width: item.key === 'Course-Week' ? 50 : 65,
                    // height: 140,
                    fixed: item.key === 'Course-Week' ? 'left' : null,
                    align: 'center',
                    render: item.key === 'Course-Week'
                        ? (h, params) => {
                            return (
                            <div class="jieshu">{params.row['Course-Week']}</div>
                            )
                        }
                        : (h, params) => {
                            // 设置随机颜色
                            let bgcolor = (() => {
                                let colorArray = ['#92c03c', '#f58181', '#ffba5d', '#54ae92', '#47ceae', '#f4a199', '#9d85cf', '#f8cd8e']
                                let random = Math.floor(Math.random() * 8)
                                return colorArray[random]
                            })()
                            // 考试教室
                            let SKJS = params.row[item.key].SKJS ? '@' + params.row[item.key].SKJS : ''
                            // 跳转至详情页
                            let location = () => {
                                sessionStorage.setItem('KCH', params.row[item.key].KCH)
                                Vm.$router.push(
                                    {
                                        name: 'details',
                                        params: Vm.$route.query
                                    }
                                )
                            }
                            let height = isMobile ? 140 : Math.floor((window.innerHeight - 100) / 5)
                            // 有数据时渲染
                            if (params.row[item.key]) {
                                return (
                                    <div class="kecheng"
                                        style={{color: '#fff', backgroundColor: bgcolor, height: height + 'px'}}
                                        on-click={location}>
                                        <div>{params.row[item.key].KCMC}{SKJS}</div>
                                    </div>
                                )
                            } else {
                                return (
                                    <div class="kecheng" style={{height: height + 'px'}}>
                                    </div>
                                )
                            }
                        }
                }
                if (isMobile) {
                    obj.width = item.key === 'Course-Week' ? 50 : 65
                } else {
                }
                console.log(obj)
                return obj
            })
            return tempArray
        },
        classDataCom() {
            const Vm = this
            let tempArray = []
            tempArray = Vm.classDataArray.map((item) => {
                let obj = {
                    'Course-Week': item['Course-Week'],
                    Monday: '',
                    Tuesday: '',
                    Wednesday: '',
                    Thursday: '',
                    Friday: '',
                    Saturday: '',
                    Weekday: ''
                }
                /**
                 * 保证同步设置数据
                 */
                function setClassData() {
                    try {
                        Vm.$store.state.SyllabusList.forEach((Syllabus) => {
                            if (Syllabus.SKJC === item['Course-Week']) {
                                switch (Syllabus.SKXQ) {
                                case '星期一' :
                                    obj.Monday = Syllabus
                                    break
                                case '星期二' :
                                    obj.Tuesday = Syllabus
                                    break
                                case '星期三' :
                                    obj.Wednesday = Syllabus
                                    break
                                case '星期四' :
                                    obj.Thursday = Syllabus
                                    break
                                case '星期五' :
                                    obj.Friday = Syllabus
                                    break
                                case '星期六' :
                                    obj.Saturday = Syllabus
                                    break
                                case '星期日' :
                                    obj.Weekday = Syllabus
                                    break
                                }
                            }
                        })
                    } catch (Error) {
                        return
                    }
                }
                setClassData()
                return obj
            })
            return tempArray
        }
    }
}
</script>

<style>
.ivu-table th {
    background-color: #fff;
}

.ivu-table .table-info {
    background-color: #2db7f5;
    color: #fff;
}
.ivu-spin-fix {
    position: fixed;
    height: 750px;
}
.ivu-table-cell {
    padding: 0;
}
.kecheng{
    padding: 5px;
}
.jieshu{
    width: 20px;
    margin: 0 auto;
    color: #2d8cf0;
    font-size: 14px;
}
</style>



